.intro-text {
    padding-top: 150px;
    font-weight: 600;
    padding-bottom: 200px;
    font-size: calc(40px + 0.4vw);
    text-align: center;
    margin-left: 2rem;
}

.subtext {
    display: inline-block; 
    text-align: left;
}

.hi {
    font-size: calc(60px + 0.4vw);
}

.images-container {
    padding-top: 25px;
}

.images {
    height: 50px;
    width: 50px;
}





.format {
    margin-left: 1rem;
}

.project-box {
    border: 1px solid blue;
}

.project_photo {
    width: 400px;
    height: 400px;
    border: 1px solid blue;
}

.row {
    display: flex;
    justify-content: center;
}

.col-project {
    padding: 20px;
}


@media screen and (max-width: 935px){
    .row {
        display: grid;
    }

}


.about-container {
    text-align: center;
}


.text-for-about {
    padding-top: 15px;
    text-align: left;
    display: inline-block; 
    font-size: calc(30px + 0.4vw);
}

img {
    height: 200px;
    width: 200px;
}
